import Vue from 'vue'
import Vuex from 'vuex'
 
Vue.use(Vuex)
 
const store = new Vuex.Store({
 // 定义状态
 state: {
 betInfos:{},
 matchdata:[],      
 betNum:''
 },
 mutations:{
 	newBetInfos(state,msg){
 		state.betInfos = msg;
 	},
 	newMatchdata(state,match){
 		state.matchdata = match;
 	},
 	newBetNum(state,num){
 		state.betNum = num;
 	}
 }
})
 
export default store


