<template>
	<div class="detailsWrap">
		<Pagehead txt='订单详情'></Pagehead>
		<ul>
			<li :class='{active:nowindex==1}' @click='nowindex=1'><router-link to='/orderdetails/orderplan'>方案详情</router-link></li>
			<li :class='{active:nowindex==2}' @click='nowindex=2'><router-link to='/orderdetails/orderstatus'>订单状态</router-link></li>
		</ul>
		<router-view></router-view>
	</div>
</template>

<script>
	import Bus from '../bus.js'
	import Pagehead from './pagehead.vue'
	export default {
		components:{
			Pagehead
		},
		data:function(){
			return{
				username:"sunshine123",
				price:'',
				nowindex:1,
				betInfos:{}
			}
		},
		created:function(){
			var that = this;
			Bus.$on('betInfomation',function(data){
				console.log(112)
				console.log(data)
				that.betInfos = data;
			})
		}
	}
</script>

<style>
	.detailsWrap ul{height: 0.8rem;border-bottom: 0.01rem solid #eee;display: flex;font-size: 0.33rem;justify-content: space-around;align-items: center;}
	.detailsWrap ul li{border-bottom: 0.05rem solid transparent;height: 100%;line-height: 0.8rem;width: 35%;}
	.detailsWrap a{color: #333;}
	.detailsWrap .active{color: #FE4C4C;border-bottom: 0.05rem solid #FE4C4C;}
	.detailsWrap .active a{color: #FE4C4C;}
</style>


