<template>
	<div class="planWrap">
		<div class="title">
			<p class="tit">奖金范围：<span>{{betInfos.bonus}}</span>元</p>
			<p class="explain">仅供参考，请以实体票为准</p>
		</div>
		<div class="betinfo">
			<h5>{{betInfos.passType}}</h5>
			<p>投注金额：<span>{{betInfos.betAmount}}</span></p>
			<p>投注信息：<span v-for='item in betInfos.passdata'>{{item}},</span>&nbsp;&nbsp;<span>{{betNum+'注'+betInfos.times+'倍'}}</span></p>
			<p>支付时间：<span>{{betInfos.payTime}}</span></p>
			<div><p>订单号：<span>{{betInfos.orderId}}</span></p><p>订单状态:<span>{{orderStatus}}</span></p></div>
		</div>
		<div class="plan">
			<table>
				<thead>
					<tr><th class="title" colspan="5">订单方案</th></tr>
					<tr>
						<th>场次</th>
						<th>主队VS客队</th>
						<th>投注项</th>
						<th>彩果</th>
						<th>胆</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for='item in matchdata'>
						<td>{{item.id}}</td>
						<td class="hg"><span>{{item.host}}</span><span>vs</span><span>{{item.guest}}</span></td>
						<td><span v-for='val in item.option'>{{val}},</span></td>
						<td>{{''}}</td>
						<td>{{''}}</td>
					</tr>
				</tbody>
			</table>
		</div>
		<Tickets></Tickets>
	</div>
</template>

<script>
	import Bus from '../bus.js'
	import Tickets from './orderplan.vue'
	export default{
		components:{
			Tickets
		},
		data:function(){
			return{
				betInfo:'',          //投注信息
				orderStatus:'待出票'
			}
		},
		computed:{           //从store.js中获取数据
			betInfos (){                            
				return  this.$store.state.betInfos;  //投注信息
			},
			matchdata (){
				return this.$store.state.matchdata;  //投注比赛内容
			},
			betNum (){
				return this.$store.state.betNum;    //购买注数
			}
		}
	}
</script>

<style>
	.planWrap{background: #E9EDEE;padding-top: 0.4rem;}
	.planWrap .title{background:#fff;padding: 0.2rem 0;}
	.planWrap .tit{font-size: 0.4rem;}
	.planWrap .tit span{color: #FE4C4C;}
    .planWrap .explain{font-size: 0.3rem;color: #7D7D7D;}
    .planWrap .betinfo{font-size: 0.3rem;color: #7D7D7D;text-align: left;margin: 0.2rem 0;background:#fff;padding: 0.2rem 0.3rem;}
    .planWrap .betinfo p{padding: 0.05rem 0;}
	.planWrap .betinfo span{color: #303030;}
	.planWrap .betinfo h5{border-bottom: 0.01rem solid #ccc;font-size: 0.33rem;padding: 0.1rem 0;margin-bottom: 0.1rem;}
	.planWrap .betinfo>div{display: flex;justify-content: space-between;}
	.planWrap .plan{margin: 0.2rem 0;background:#fff;padding: 0.2rem 0.1rem;}
	.planWrap table{width: 100%;border: 0.01rem solid #ccc; border-collapse: collapse;font-size: 0.2rem;color:#303030; }
	.planWrap table .title{text-align: left; padding-left: 0.2rem;}
	.planWrap table th,.planWrap table td{border: 0.01rem solid #ccc;border-collapse: collapse;}
	.planWrap table th,.planWrap table td:nth-child(1){width:15% }
	.planWrap table th,.planWrap table td:nth-child(2){width:32% }
	.planWrap table th,.planWrap table td:nth-child(3){width:25% }
	.planWrap table th,.planWrap table td:nth-child(4){width:18% }
	.planWrap table th,.planWrap table td:nth-child(5){width:9% }
	.planWrap table .hg span{display:block; }
	.planWrap table th{padding: 0.2rem 0;}
</style>


